import { _decorator, Component, Sprite, Enum } from 'cc';
import { ColorManager } from './ColorManager';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

export enum ColorType {
    BgColor = 0,
    TextColor,
    HintColor,
    LinkColor,
    ButtonColor,
    ButtonTextColor,
    SecondaryBgColor,
    HeaderBgColor,
    BottomBarBgColor,
    AccentTextColor,
    SectionBgColor,
    SectionHeaderTextColor,
    SectionSeparatorColor,
    SubtitleTextColor,
    DestructiveTextColor
}

@ccclass('ColorSetter')
export class ColorSetter extends Component {

    @property
    isDefault: boolean = false;

    @property(Sprite)
    sprite: Sprite = null;

    @property({ type: Enum(ColorType) })
    colorType: ColorType = ColorType.BgColor;

    start() {
        this.init();
    }

    async init(){

        // while (!TelegramWebApp.Instance.isInited) {
        //     await new Promise(resolve => setTimeout(resolve, 100));
        // }

        if (!this.sprite) {
            console.error('Sprite undefined');
            return;
        }

        const colorManager = ColorManager.Instance;
        if (!colorManager) {
            console.error('ColorManager undefined');
            return;
        }

        let color = null;
        switch (this.colorType) {
            case ColorType.BgColor:
                color = colorManager.getBgColor();
                break;
            case ColorType.TextColor:
                color = colorManager.getTextColor();
                break;
            case ColorType.HintColor:
                color = colorManager.getHintColor();
                break;
            case ColorType.LinkColor:
                color = colorManager.getLinkColor();
                break;
            case ColorType.ButtonColor:
                color = colorManager.getButtonColor();
                break;
            case ColorType.ButtonTextColor:
                color = colorManager.getButtonTextColor();
                break;
            case ColorType.SecondaryBgColor:
                color = colorManager.getSecondaryBgColor();
                break;
            case ColorType.HeaderBgColor:
                color = colorManager.getHeaderBgColor();
                break;
            case ColorType.BottomBarBgColor:
                color = colorManager.getBottomBarBgColor();
                break;
            case ColorType.AccentTextColor:
                color = colorManager.getAccentTextColor();
                break;
            case ColorType.SectionBgColor:
                color = colorManager.getSectionBgColor();
                break;
            case ColorType.SectionHeaderTextColor:
                color = colorManager.getSectionHeaderTextColor();
                break;
            case ColorType.SectionSeparatorColor:
                color = colorManager.getSectionSeparatorColor();
                break;
            case ColorType.SubtitleTextColor:
                color = colorManager.getSubtitleTextColor();
                break;
            case ColorType.DestructiveTextColor:
                color = colorManager.getDestructiveTextColor(); 
                break;
            default:
                console.error('Unknown type of color');
                break;
        }

        if (color) {
            this.sprite.color = color;
        }
    }
}